.player {
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  &__time-control {
    width: 50%;
    display: flex;
    align-items: center;
  }

  &__current-time,
  &__duration {
    padding: 1rem;
    user-select: none;
  }

  &__track {
    position: relative;
    width: 100%;
    height: 1rem;
    border-radius: 1rem;
    overflow: hidden;

    input {
      width: 100%;
      -webkit-appearance: none;
      background: transparent;
      cursor: pointer;
    }

    input[type="range"]:focus {
      outline: none;
    }

    input[type="range"]::-webkit-slider-thumb {
      -webkit-appearance: none;
      height: 16px;
      width: 16px;
    }

    input[type="range"]::-moz-slider-thumb {
      -webkit-appearance: none;
      background: transparent;
      border: none;
    }
  }

  &__animate-track {
    background: rgb(204, 204, 204);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;

    transform: translateX(0%);
    pointer-events: none;
  }

  &__play-control {
    width: 50%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;

    svg {
      cursor: pointer;
    }
  }

  @media screen and(max-width: 768px) {
    &__time-control {
      width: 90%;
    }

    &__play-control {
      width: 60%;
    }
  }
}
