.library {
  position: fixed;
  top: 0;
  left: 0;

  width: 20rem;
  height: 100%;

  background-color: rgb(255, 255, 255);
  box-shadow: 2px 2px 50px rgb(204, 204, 204);
  overflow: scroll;

  transform: translateX(-100%);
  transition: all 0.5s ease;

  &__title {
    padding: 2rem;
    user-select: none;
  }

  &.active {
    transform: translateX(0%);
  }

  @media screen and(max-width: 768px) {
    width: 100%;
  }
}
