.song {
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  &__image {
    width: 30%;
    max-width: 400px;
    border-radius: 50%;

    user-select: none;
  }

  &__name {
    padding: 3rem 1rem 1rem 1rem;
  }

  &__artist {
    font-size: 1rem;
  }

  @media screen and(max-width: 768px) {
    &__image {
      width: 60%;
    }
  }
}
