.library-item {
  display: flex;
  align-items: center;

  padding: 1rem 2rem 1rem 2rem;

  transition: background 0.5s ease;
  cursor: pointer;

  &__img {
    width: 30%;
    user-select: none;
  }

  &__description {
    padding-left: 1rem;
  }

  &__song-name {
    padding: 0;
    font-size: 1rem;
  }

  &__artist-name {
    padding: 0;
    font-size: 0.7rem;
  }

  &.selected {
    background-color: bisque;
  }

  &:hover {
    background-color: rgb(222, 222, 255);
  }

  @media screen and(max-width: 768px) {
    &__song-name {
      font-size: 1.4rem;
    }

    &__artist-name {
      font-size: 1rem;
    }
  }

  @media screen and(max-width: 320px) {
    &__song-name {
      font-size: 1rem;
    }

    &__artist-name {
      font-size: 0.7rem;
    }
  }
}
