.nav {
  display: flex;
  justify-content: space-around;
  align-items: center;

  min-height: 10vh;

  &__title {
    user-select: none;
  }

  &__button {
    z-index: 2;
    padding: 0.5rem;

    background: transparent;

    border: 2px solid rgb(65, 65, 65);
    transition: all 0.3s ease;

    cursor: pointer;
    user-select: none;

    &:hover {
      background: rgb(65, 65, 65);
      color: white;
    }
  }
}
